.gameScore {
    display: grid;
    justify-items: center;
    align-content: flex-start;
    row-gap: 50px;
    background-color: rgb(74, 143, 111);
}

.scoreParameters {
    text-align: center;
    background-color: rgb(69, 94, 94);
    padding: 50px;
    padding-right: 100px;
    padding-left: 100px;
    border-radius: 10px;
}

.scoreParameters * {
    margin: 15px;
}

.scoreInfo {
    font-size: 100px;
    font-family: sans-serif;
    color: white;
}

.scoreTitle {
    font-size: 110px;
    font-family: "Lato", sans-serif;
    color: rgb(0, 255, 157);
    text-shadow: 3px 3px black;
}