.header {
    background-color: rgb(141, 199, 179);
    padding: 30px;
    padding-left: 50px;
    padding-right: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header h1 {
    font-size: 70px;
    font-family: "Lato";
    font-weight: 400;
    text-shadow: 1.5px 1.5px 2px white;
    user-select: none;
}

.headerControls {
    margin-left: 500px;
}

.headerControls button {
    margin: 10px;
    width: 250px;
    padding: 15px;
    font-size: 18px;
    font-family: 'Lato', sans-serif;
    background-color: rgb(125, 228, 142);
    border: none;
    outline: none;
    border-radius: 10px;
    cursor: pointer;
    transition-property: color, background-color;
    transition-duration: 0.55s, 0.4s;
    user-select: none;
}

.headerControls button:hover {
    background-color: rgb(125, 216, 140);
    color: rgb(255, 255, 255);
}

.headerControls button:active {
    transition-duration: 0.02s 0.02s;
    background-color: rgb(162, 228, 173);
    color: black;
}