.phrase {
    height: 650px;
}

.gameArea {
    display: grid;
    justify-items: center;
    align-items: flex-start;
    position: relative;
}

.gameInput {
    width: 500px;
    font-size: 25px;
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.548);
    border: none;
    border-bottom: 2px solid black;
    outline: none;
    text-align: center;
    border-radius: 5px;
}

.wordsCounter {
    position: absolute;
    right: 50px;
    top: 20px;
    font-size: 35px;
    color: white;
    font-family: monospace;
    user-select: none;
}