.phraseWord {
    color: white;
    font-family: "Lato";
    font-size: 50px;
    display: inline-block;
    margin-right: 20px;
    margin-bottom: 40px;
    user-select: none;
    padding: 5px;
}

.correct {
    color: rgb(0, 197, 0);
}

.wrong {
    color: rgb(161, 75, 75);
}

.marked {
    text-decoration: underline;
}

.actual {
    background-color: rgba(128, 128, 128, 0.253);
}